.AboutContainer {
    background-color: #ECD8CF;
    display: grid;
    padding: 50px;
}

.AboutSpecial {
    display: grid;
}

.AboutCard {
    display: flex;
}

.AboutText {
    max-width: 50%;
    color: #1D5A40;
}

.AboutHeading {
    font-size: xx-large;
}
.AboutPara {
    margin: 50px 0px 50px 0px;
    font-size: large;
}

.Aboutimg {
    display: flex;
    position: relative;
    justify-content: flex-end;
    height: 400px;
    padding: 50px;
    border-radius: 50px;
    margin: 0px 0px 0px 100px;
}

.Aboutimg2 {
    display: flex;
    position: relative;
    justify-content: flex-end;
    height: 400px;
    padding: 50px;
    border-radius: 50px;
    margin: 0px 110px 0px 0px;
}

.aboutImage {
    border: 5px solid #ffffff;
    border-radius: 10%;
}


.AboutInstructor {
    display: flex;
    justify-content: center;
}

.InstructorCard {
    border: 5px solid #ffffff;
    border-radius: 10%;
    display: grid;
    margin: 50px;
    max-width: 40%;
    background-color: #ffffff;
}

.Instructorimg{
    padding: 40px;
    height: auto;
    border-radius: 50px;
}

.InstructorHeading {
    padding: 10px;
    text-align: center;
    color: #1D5A40;
    font-size:xx-large;
}

.AboutInstructor {
    color: #1D5A40;
    text-align: center;
    font-size: large ;
    padding: 40px;
}

.InstructorImage {
    height: 300px;
    border: 5px solid #ECD8CF;
    border-radius: 10%;
}