.navbar {
    display: flex;
    position: fixed;
    height: 50px;
    width: 100%;
    background: #ECD8CF;
    overflow: visible;
    flex-direction: row;
    z-index: 2;
    justify-content: center;
}

.navdiv1 {
    display: flex;
    position: relative;
    height:  100%;
    width: 500px;
    margin: 0px 80px 0px 30px;
}

.navdiv2 {
    display: flex;
    position: relative;
    margin: 0px 10px 0px 860px;
    height: 100%;
}
a {
    text-decoration: none;
  }

.navlinks {
    margin: 25px 20px 0px 20px;
    font-size: 20px;
    color: #1D5A40;
}

.navdiv3 {
    display: flex;
    position: relative;
    height: 400%;
    width: 200px;
    min-width: 200px;
    margin: 40px 0px 0px 0px;
    background-color: #ECD8CF;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    align-self: center;
    right: 0;;
    z-index: 1;
}

.img1 {
    display: flex;
    border-radius: 50%;
    height: 90%;
    width: 90%;
}


@media screen and (max-width: 960px) {
    .navbar {
        display: flex;
        position: fixed;
        height: 50px;
        width: 100%;
        background: #ECD8CF;
        z-index: 2;
        justify-content: center;
    }

    .navdiv1 {
        display: flex;
        position: relative;
        height:  50px;
        width: 100%;
        margin: 0px 80px 0px 565px;
        justify-content: center;
    }

    .navdiv2 {
        height: 0xp;
    }

    .navlinks {
        font-size: 0px;
    }

    .navdiv3 {

        height: 0px;


    }

}