.home {
    display: flex;
    position: relative;
    height: 100%;
    width: 100%;
    align-items: center;
    align-self: center;
    align-content: center;
    justify-content: center;
    background-color:#ECD8CF;
    text-align: center;

}

.bgimage {
    display: flex;
    position: relative;
    height: 100%;
    width: 100%;
    border-radius: 0px 0px 10% 10%;
}

.title {
    color: aliceblue;
    position: absolute;
    z-index: 1;
    font-size: xx-large;
    transform: translate(3%, -170%)
}