.Testimonialsheading {
    background-color: #ECD8CF;
    font-size: xx-large;
    color: #1D5A40;
    padding: 20px;
}

.Testimonials {
    display: flex;
    background-color: #ECD8CF;
    justify-content: left;
    overflow-x: scroll;
    overflow-y: hidden;
}

.TestimonialsCards {
    display: grid;
    margin: 5px 20px 25px 20px;
    height: 350px;
    width: 800px;
    min-width: 500px;
    border: 3px solid #fff;
    border-radius: 10px;
    padding: 30px;
}

.cardText {
    text-align: left;
    color: #1D5A40;
    font-size: x-large;
}

.cardBy {
    text-align: right;
    color: #1D5A40;
    font-size: medium;
}